<template>
  <div class="search-result">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      :error.sync="error"
      error-text="加载失败，请点击重试"
      @load="onLoad"
    >
      <van-cell
        v-for="(article, index) in list"
        :key="index"
        :title="article.title"
      />
    </van-list>
  </div>
</template>

<script>
import { getSearchResult } from '@/api/search'
export default {
  name: 'searchResult',
  props: {
    searchText: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1,
      perPage: 20,
      error: false
    }
  },
  created() {},
  methods: {
    async onLoad() {
      try {
        //  请求获取数据
        const { data } = await getSearchResult({
          page: this.page, // 页码
          per_page: this.perPage, // 每页数量
          q: this.searchText // 查询关键词
        })

        // 模拟随机失败的情况
        // if (Math.random() > 0.5) {
        //   JSON.parse('sfad')
        // }

        const { results } = data.data

        // 判断是否还有数据
        if (results.length) {
          // 有，则将数据添加到数组列表中
          this.list.push(...results)

          // 将本次加载中的 loading 关闭
          this.loading = false
          // 更新获取下一个数据页码
          this.page++
        } else {
          // 没有，则将加载状态 finished 设置为结束
          this.finished = true
        }
      } catch (err) {
        // 展示加载失败的提示
        this.error = true

        this.loading = false
      }
    }
  }
}
</script>

<style lang="less" scoped></style>
