<template>
  <div class="search-suggesiton">
    <van-cell
      icon="search"
      v-for="(text, index) in suggestions"
      :key="index"
      @click="$emit('search', text)"
    >
      <div slot="title" v-html="highlight(text)"></div>
    </van-cell>
  </div>
</template>

<script>
import { getSearchSuggestions } from '@/api/search'
// 按需加载防抖工具包
import { debounce } from 'lodash'
export default {
  name: 'searchSuggesiton',
  props: {
    searchText: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      suggestions: [] // 联想建议数据列表
    }
  },
  watch: {
    searchText: {
      // 当 searchText 发生变化的时候就会调用 handler 函数
      // handler(value) {
      //   this.loadSearchSuggestions(value)
      // },
      // debounce 函数
      // 参数1：一个函数
      // 参数2：延迟时间，单位毫秒
      // 返回值：防抖后的函数
      handler: debounce(function(value) {
        this.loadSearchSuggestions(value)
      }, 200),
      immediate: true // 改回调函数会在侦听开始之后立即调用
    }
  },
  created() {},
  methods: {
    async loadSearchSuggestions(q) {
      try {
        const { data } = await getSearchSuggestions(q)
        this.suggestions = data.data.options
      } catch (err) {
        this.$toast('数据获取失败，请稍后重试')
      }
    },
    highlight(text) {
      const highlightStr = `<span style="color:#3296fa">${this.searchText}</span>`
      // 正则表达式 // 中间的内容都会当作匹配字符来使用，而不是数据变量
      // 如果根据数据变量动态的创建正则表达式，则手动 new RegExp
      // RegExp 正则表达式构造函数
      //  参数1：匹配模式字符串，它会根据这个字符串创建正则对象
      //  参数2：匹配模式
      const reg = new RegExp(this.searchText, 'gi')
      return text.replace(reg, highlightStr)
    }
  }
}
</script>

<style lang="less" scoped></style>
